/* src/styles/App.css */

.app-layout {
  display: flex;
  min-height: 100vh;
  background-color: #111;
  color: white;
  font-family: 'Segoe UI', sans-serif;
}

/* Sidebar container */
.sidebar {
  width: 280px;
  background-color: #181818;
  position: fixed;
  height: 100vh;
  overflow-y: auto;
}

/* Push main-content to right of fixed sidebar */
.main-content {
  margin-left: 280px;
  flex: 1;
  display: flex;
  flex-direction: column;
  padding-top: 64px; /* Accounts for fixed navbar */
  overflow: hidden;
}

/* Tab content area */
.section-container {
  flex: 1;
  padding: 1.5rem;
  overflow-y: auto;
  animation: fadeIn 0.4s ease;
}

/* Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.section-transition {
  opacity: 0;
  transform: translateY(10px);
  transition: all 0.4s ease;
}

.section-transition.active {
  opacity: 1;
  transform: translateY(0);
}

/* Responsive */
@media (max-width: 768px) {
  .app-layout {
    flex-direction: column;
  }

  .sidebar {
    position: relative;
    width: 100%;
    height: auto;
  }

  .main-content {
    margin-left: 0;
    padding-top: 64px;
  }
}
